/**
 * THIS FILE HAS BEEN IS MODIFIED.
 *
 * Prism.js is blocking main thread (JS Engine) is very hardly. Most of the code
 * to highlight is below first viewing page, and even with manual triggering,
 * registering Prism is very consuming job.
 *
 * By idle callback we move initialization of original code at the moment, when
 * CPU is doing nothing and this will not block the main thread. The code in
 * this case will be executed after onload event.
 *
 * At the moment of writing this "thing" scripting performance was dropped from
 * avg 120ms to 75ms - it's 37.50% faster! This was tested on very decent PC,
 * for mobile devices this will be a lot better.
 *
 * Highlight code syntax is not primary site job. So we can slow it down.
 *
 * @todo Look for backend code highlight. Because Prism is also modifies DOM
 *   very hard, this is also triggering a lot of Style & Layout operations which
 *   is very CPU intense.
 */
