  let trigger;
  if (window.requestIdleCallback) {
    trigger = (callback) => {
      requestIdleCallback(callback)
    }
  }
  else {
    // Fallback for browsers doesn't support IDLE callbacks.
    trigger = (callback) => {
      callback()
    }
  }

